	module.exports = {"data":{"post":{"html":"<p>The weather was much too nice for early February this weekend. Yesterday Mama bought an electric hedge trimmer and attacked the wild roses. Today we took Christopher to the <a href=\"http://www.wildpark-schwarze-berge.de\">Wildpark Schwarze Berge</a>. It seemed like half of Hamburg had the same idea, but we had a good time and Christopher loved the giant slide which is actually intended for kids twice his age.</p>\n<p>This is mostly for <a href=\"http://www.vfth.com\">Alwin</a>, with whom I sometimes share details of the German health system. <a href=\"http://www.abendblatt.de/contents/ha/news/politik/html/020202/BURATBES4.HTM\">Hamburger Abendblatt</a> (in German): The German Bundesrat (upper house of parliament) approved \"Aut Idem\" prescriptions, meaning that pharmacists in most cases will be to choose the cheapest drug (i.e. generics). At the same time, the Bundesrat rejected flat rate  (as opposed to daily rate) hospital billing, sending the proposal to the German equivalent of the conference committee. <a href=\"http://www.faz.com/IN/INtemplates/eFAZ/docmain.asp?rub=&#123;B1311FCC-FBFB-11D2-B228-00105A9CAF88&#125;&doc=&#123;648F221E-7C12-4695-ACF6-6D8CF948DCFE&#125;\">FAZ</a> has a short English report.</p>","fields":{"title":"Aut Idem","date":"03 Feb 2002","url":"https://papascott.de/2002-02-03-aut-idem/"}}},"pathContext":{"slug":"/2002-02-03-aut-idem/","prev":{"fields":{"title":"Raining Red Roses","date":"01 Feb 2002","slug":"/2002-02-01-raining-red-roses/"}},"next":{"fields":{"title":"Cereal Bowl","date":"04 Feb 2002","slug":"/2002-02-04-cereal-bowl/"}}}}

